import jwx from '../../common/weiPay/jws.js'
import {
	setClipboardData,
	getClipboardData
} from '@/uni_modules/u-clipboard/js_sdk'
import ssPaymentPassword from '../../components/sanshui-payment-password'

export default {
	components: {
		ssPaymentPassword
	},
	data() {
		return {
			acer_truncatum: 0.00, //金元宝
			gold_balance: 0.00, //银元宝
			balance: 0.00, //用户余额
			mainData: {
				headImg: "",
				nickName: "",
				nvitationCode: "",
				user_type: 0,
				up: 0
			},
			rankItem: [{
				id: 0,
				name: '普通用户',
				logo: '/static/images/v0.png'
			}, {
				id: 1,
				name: '青铜分店',
				logo: '/static/images/v1.png'
			}, {
				id: 2,
				name: '白银分店',
				logo: '/static/images/v2.png'
			}, {
				id: 3,
				name: '黄金分店',
				logo: '/static/images/v3.png'
			}, {
				id: 4,
				name: '分公司',
				logo: '/static/images/v4.png'
			}],
			upData: {
				money: 0,
				name: '',
				type: 1,
				id: 0
			}
		};
	},
	onShow: function(e) {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		var user = uni.getStorageSync('uerInfo');
		this.mainData.nickName = user.user_name;
		this.mainData.headImg = user.user_head;
		this.mainData.nvitationCode = user.invitation;
		this.mainData.user_type = user.user_type;
		this.mainData.up = user.up;
		this.getUserData();
	},
	methods: {
		dump(url) {
			uni.navigateTo({
				url: url
			});
		},
		copyCode() {
			//复制
			var _this = this;
			setClipboardData(_this.mainData.nvitationCode).then(() => {
				uni.showToast({
					title: '复制成功',
				});
			})
		},
		getUserData() {
			//获取用户信息
			var _this = this;
			_this.$request.user
				.getUserData({})
				.then(data => {
					if (data.status == 1) {
						_this.balance = data.data.balance;
						_this.acer_truncatum = data.data.acer_truncatum;
						_this.gold_balance = data.data.gold_balance;
						this.mainData.user_type = data.data.user_type;
						this.mainData.up = data.data.up;
						if (_this.mainData.up == 1) {
							uni.setTabBarBadge({
								index: 3,
								text: '1'
							})
						} else {
							uni.setTabBarBadge({
								index: 3
							})
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		up() {
			//升级
			var _this = this;
			if (_this.mainData.up == 0) {
				uni.showToast({
					icon: 'none',
					title: '未满足升级条件'
				});
				return false;
			}
			uni.showLoading({
				title: '数据查询中'
			});
			var _this = this;
			_this.$request.user
				.getUp({})
				.then(data => {
					if (data.status == 1) {
						_this.upData = data.data;
						this.$refs['showUp'].open();
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
					uni.hideLoading();
				})
				.catch(err => {
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		closeUp() {
			//关闭升级
			this.$refs['showUp'].close();
		},
		openPwd() {
			var _this = this;
			if (_this.addressItem.id <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请选择收货地址'
				});
				return false;
			}
			//验证密码
			this.$refs.paymentPassword.modalFun('show');
		},
		upBuy(e) {
			//确认购买
			var _this = this;
			uni.showLoading({
				title: '数据提交中'
			});
			_this.$request.common
				.paySundries({
					type: 2
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						_this.closeUp();
						_this.pay(data.msg);
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					console.log(err)
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		pay(orderInfo) {
			jwx({
				nonceStr: orderInfo.nonceStr,
				timeStamp: orderInfo.timeStamp,
				package: orderInfo.package,
				signType: orderInfo.signType,
				paySign: orderInfo.paySign
			}, res => {
				uni.showToast({
					icon: 'none',
					title: '充值成功'
				});
				setTimeout(() => {
					_this.getUserData();
				}, 1000)
			}, fail => {
				console.log(fail)
				uni.showToast({
					icon: 'none',
					title: '支付失败'
				});
			})
		}
	}
};
